import React from 'react';
import { withTranslation } from "react-i18next"
import { Link } from 'gatsby';

import { Layout } from "../components/Layout";
import SEO from "../components/SEO";

const I18N_ID = 'Landing';

/**
 * Landing Page
 * 
 * @param {TFunction} t i18n function
 * @param {string} title page title
 */
const PageLanding = ({
  t, title,
}) => (
  <Layout>
    {/* page title */}
    <SEO title={title} />

    <h1> {t('hello', {text: title})} </h1>

    <Link to="/sample-themes/landing/">Go to Landing-page</Link>
    <br/>
    <Link to="/under-construction/">Go to Under-construction</Link>
  </Layout>
);

export default withTranslation(I18N_ID)(PageLanding);
