import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import LandingPageView from '../views/page-landing';

const IndexPage = () => {
  const { site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title,
          languages {
            defaultLangKey,
          }
        }
      }
    }
  `);

  return (
    <LandingPageView title={site.siteMetadata.title}/>
  );
}

export default IndexPage;
